const client = require("../client");
const utils = require("../utils");
/**
 * @module /RfCommunication
 */
module.exports = {
  /**
   * @description clear_msgNone
   * @param {int} moduleIndex 
   * @returns {Promise<float>} None
   */
  getMsg(moduleIndex) {
    utils.checkNotNull(moduleIndex);
    return client._doReport(`rfCommunication${moduleIndex}.get_msg()`);
  },
  /**
   * @description get_msgNone get_unread_msg_countread
   * @param {int} moduleIndex 
   */
  clearMsg(moduleIndex) {
    utils.checkNotNull(moduleIndex);
    return client._doCommand(`rfCommunication${moduleIndex}.clear_msg()`);
  },
  /**
   * @description 32
   * @param {int} moduleIndex 
   * @returns {Promise<int>} 0~32
   */
  getUnreadMsgCount(moduleIndex) {
    utils.checkNotNull(moduleIndex);
    return client._doReport(
      `rfCommunication${moduleIndex}.get_unread_msg_count()`
    );
  },
  /**
   * @description 
   * @param {int} moduleIndex 
   * @returns {Promise<float>} None
   */
  read(moduleIndex) {
    utils.checkNotNull(moduleIndex);
    return client._doReport(`rfCommunication${moduleIndex}.read()`);
  },
  /**
   * @description 
   * @param {int} moduleIndex 
   * @param {float} number 
   */
  send(moduleIndex, number) {
    utils.checkNotNull(moduleIndex);
    utils.checkNotNull(number);
    return client._doCommand(`rfCommunication${moduleIndex}.send(${number})`);
  },
  /**
   * @description 
   * @param {int} moduleIndex 
   * @param {str} name 
   */
  init(moduleIndex, name = "public") {
    utils.checkNotNull(moduleIndex);
    utils.checkNotNull(name);
    name = name.toString().replace(/\'/g, "\\'");
    return client._doCommand(`rfCommunication${moduleIndex}.init('${name}')`);
  },
  /**
   * @description 
   * @param {int} moduleIndex 
   * @returns {Promise<bool>} TrueFalse
   */
  isButtonPressed(moduleIndex) {
    utils.checkNotNull(moduleIndex);
    return client._doReport(
      `rfCommunication${moduleIndex}.is_button_pressed()`
    );
  },
  /**
   * @description 
   * @param  {int} moduleIndex 
   * @returns {Promise<int>}
   */
  getFirmwareVersion(moduleIndex) {
    utils.checkNotNull(moduleIndex);
    return client._doReport(
      `rfCommunication${moduleIndex}.get_firmware_version()`
    );
  },
  /**
   * @description LED
   * @param  {int} moduleIndex 
   * @param  {int} rgb '': 1,'':2,'':3,'':4,'':5,'':6,'': 7,'': 8
   */

  setOnboardRGB(moduleIndex, rgb) {
    utils.checkNotNull(moduleIndex);
    utils.checkNotNull(rgb);
    return client._doCommand(
      utils.setOnboardRGB(`rfCommunication${moduleIndex}`, rgb)
    );
  },
  /**
   * @description bool
   * @param  {int} moduleIndex 
   */
  regButton(moduleIndex, callback) {
    utils.checkNotNull(moduleIndex);
    client._eventRegister(`rfCommunication${moduleIndex}`, "button", callback);
  },
  /**
   * @description float
   * @param  {int} moduleIndex 
   */
  regMsgReceived(moduleIndex, callback) {
    utils.checkNotNull(moduleIndex);
    client._eventRegister(
      `rfCommunication${moduleIndex}`,
      "msg_received",
      callback
    );
  },
  /**
   * @description 
   * @param  {int} moduleIndex 
   */
  unregButton(moduleIndex) {
    client.eventUnregister(`rfCommunication${moduleIndex}`, "button");
  },
  /**
   * @description 
   * @param  {int} moduleIndex 
   */
  unregMsgReceived(moduleIndex) {
    client.eventUnregister(`rfCommunication${moduleIndex}`, "msg_received");
  }
};
